


#formContent {
  text-align: center;
  margin: 10rem;
  width: auto;
}

#email,
#password,
#submit {
  margin: 1rem auto;
  display: block;
  text-align: center;
  width: 80%;
  padding: 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #f5f5f5;
  transition: background-color 0.3s ease;
}

#email:focus,
#password:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.8);
  background-color: #fff;
}

.submitBTN {
  text-align: center;
}

.btn {
  font-size: 1rem;
  color: #fff;
  border-radius: 4px;
  padding: 0.8rem 1.5rem;
  background-color: #4caf50;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #45a049;
}

div.error {
  padding: 10px;
  background: #ffebee;
  border: 1px solid #f44336;
  color: #f44336;
  border-radius: 4px;
  margin: 20px 0;
}

input.error {
  border: 1px solid #f44336;
}

@media screen and (max-width: 768px) {
  #formContent {
    margin: 5rem auto;
    width: 80%;
    text-align: center;
  }

  #email,
  #password,
  #submit {
    width: 100%;
    padding: 0.8rem 0.4rem;
    margin: 0.8rem 0.4rem;
  }

  .btn {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

@media screen and (min-width: 768px) {
  #formContent {
    margin: 2rem;
  }

  #email,
  #password,
  #submit {
    width: 40%;
    font-size: 1rem;
  }

  .btn {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

